/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem; }

p {
  color: #FFFFFF;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #0da6f3;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

.header {
  background-color: #1a1b15; }
  .header .contenido-header {
    padding: 2.5rem 0; }
    @media (min-width: 768px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #FFFFFF;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem, 4rem #444; }

.navegacion-principal {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .navegacion-principal a {
    color: #FFFFFF;
    font-size: 2rem; }
    .navegacion-principal a:hover {
      color: #f53756; }

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#e4f5fc+0,bfe8f9+50,9fd8ef+51,2ab0ed+100&0.5+0,0.5+100;Blue+Gloss+%235 */
