    background: -moz-linear-gradient(-45deg, rgba(149, 149, 149, 0.5) 0%, rgba(13, 13, 13, 0.5) 46%, rgba(1, 1, 1, 0.5) 50%, rgba(10, 10, 10, 0.5) 53%, rgba(78, 78, 78, 0.5) 76%, rgba(56, 56, 56, 0.5) 87%, rgba(27, 27, 27, 0.5) 100%);
    /* FF3.6-15 */
