    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(149, 149, 149, 0.5) 0%, rgba(13, 13, 13, 0.5) 46%, rgba(1, 1, 1, 0.5) 50%, rgba(10, 10, 10, 0.5) 53%, rgba(78, 78, 78, 0.5) 76%, rgba(56, 56, 56, 0.5) 87%, rgba(27, 27, 27, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80959595', endColorstr='#801b1b1b',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1; }
  .contenido-video h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #FFFFFF;
    font-size: 7rem; }
    .contenido-video h2 span {
      background-color: #f53756;
      font-size: 2rem;
      padding: 1rem 2rem; }

.sobre-nosotros {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .sobre-nosotros {
      display: grid;
      gap: 5rem;
      grid-template-columns: repeat(2, 1fr); } }

.contenido-nosotros {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .contenido-nosotros h2 {
    font-size: 7rem;
    color: #1a1b15; }
  .contenido-nosotros .fecha {
    color: #f53756;
    font-weight: 700; }
  .contenido-nosotros .texto {
    line-height: 1.5;
    color: #1a1b15;
    text-align: justify; }

.nombre-escenario {
  text-align: center; }

.productos {
  background-color: #1a1b15;
  padding: 5rem; }
  .productos h2 {
    font-size: 7rem;
    color: #FFFFFF; }

@media (min-width: 768px) {
  .contenedor-productos {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr); } }

.contenedor-productos .producto {
  flex-shrink: 0;
  /*Las siguientes tres lineas se pueden resumir en una sola
    border-style: solid;
    border-color: #B5B5B5;
    border width: 1px;         */
  border: 15px solid #B5B5B5;
  /*Esta linea es lo mismo que las anteriores 3*/
  background-color: #FFFFFF;
  /*Esta linea hace que se le agregue un fondo a cada una de los bordes*/
  margin-bottom: 2rem; }
  .contenedor-productos .producto img {
    display: block;
    margin: auto;
    width: 500px;
    height: 500px; }

.contenedor-productos .contenido-producto {
  padding: 2rem; }
  .contenedor-productos .contenido-producto h3 {
    color: #1a1b15;
    font-size: 4rem; }
  .contenedor-productos .contenido-producto p {
    margin: 0;
    color: #1a1b15;
    line-height: 2rem;
    text-align: justify; }
  .contenedor-productos .contenido-producto span {
    color: #006400; }

.precios-caracteristicas {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
  flex: 1;
  max-width: 500px; }

.precios-caracteristicas li {
  display: flex; }

.precios-caracteristicas li img {
  margin-right: 2rem; }

.contacto {
  background-color: #FFFFFF;
  padding: 5rem 0; }
  .contacto h2 {
    color: #1a1b15;
    font-size: 7rem; }

@media (min-width: 768px) {
  .contenedor-tabla {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(2, 1fr); } }

.contenedor-tabla .tabla {
  padding-top: 1rem; }

.contenedor-tabla .nombre-tabla {
  color: #1a1b15;
  font-size: 3rem;
  font-weight: 700;
  text-align: center; }
  .contenedor-tabla .nombre-tabla:after {
    content: '';
    display: block;
    margin: 1rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.lista {
  padding: 0;
  list-style: none;
  font-size: 2rem; }
  .lista li {
    padding: 1.96078rem;
    font-weight: 900; }
    .lista li span {
      font-weight: 400; }

.bg-verde {
  background-color: #4cb8b3; }

.bg-verde .lista li:nth-child(odd) {
  background-color: #3b9691; }

.lista i {
  color: #1a1b15;
  margin-right: 20px;
  font-size: 4rem; }

.lista i:hover {
  cursor: pointer;
  color: #FFFFFF; }

.footer {
  background-color: #1a1b15;
  padding: 2.5rem;
  text-align: center; }
  .footer p {
    color: #FFFFFF;
    margin: 0; }
